import React, { useState } from "react";
import {
  Form,
  Input,
  Button,
  Typography,
  Card,
  Divider,
  Space,
  Col,
  Row,
  Modal,
} from "antd";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { clearCart } from "../../store/features/cart/cartSlice";
import Header from "../../components/header/Header";
import Footerr from "../../components/footer/Footer";
const { Title } = Typography;

export default function Checkout() {
  const { items, totalPrice } = useSelector((state) => state.cart);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [formValues, setFormValues] = useState({});

  const onFinish = (values) => {
    setFormValues(values);
    setIsModalVisible(true);
  };

  const handleOk = () => {
    console.log("Payment confirmed:", formValues);
    setIsModalVisible(false);
    dispatch(clearCart());
    navigate(`/#`);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      {" "}
      <Header />
      <div className="sectionContainer">
        <div className="sectionContent">
          <div style={{ padding: "20px" }}>
            <Title level={2}>Trang Thanh Ton</Title>

            <Row gutter={[16, 16]}>
              {/* Danh sch sn phm */}
              <Col xs={24} md={12}>
                <Card style={{ marginBottom: "20px" }}>
                  <Title level={4}>Thng tin n hng</Title>
                  {items.map((item) => (
                    <div
                      key={item.id}
                      className="cart-item"
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between",
                        marginBottom: "10px",
                      }}
                    >
                      <Space>
                        <h3 style={{ margin: 0 }}>{item.itemName}</h3>
                        <p style={{ margin: 0 }}>
                          Gi:{" "}
                          {item.price
                            ? item.price.toLocaleString() + " VND"
                            : "Cha c gi"}
                        </p>
                        <p style={{ margin: 0 }}>S lng: {item.quantity}</p>
                      </Space>
                    </div>
                  ))}
                  <div className="cart-item">
                    <h3 style={{ margin: 0 }}>
                      Tng cng:{" "}
                      <span>{totalPrice.toLocaleString() + " VND"}</span>
                    </h3>
                  </div>
                </Card>
              </Col>

              <Col xs={24} md={12}>
                <Form layout="vertical" onFinish={onFinish}>
                  <Form.Item
                    label="H v tn"
                    name="name"
                    rules={[
                      { required: true, message: "Vui lng nhp h v tn!" },
                    ]}
                  >
                    <Input placeholder="Nhp h v tn" />
                  </Form.Item>

                  <Form.Item
                    label="S in thoi"
                    name="phone"
                    rules={[
                      {
                        required: true,
                        message: "Vui lng nhp s in thoi!",
                      },
                    ]}
                  >
                    <Input placeholder="Nhp s in thoi" />
                  </Form.Item>

                  <Form.Item
                    label="a ch giao hng"
                    name="address"
                    rules={[
                      {
                        required: true,
                        message: "Vui lng nhp a ch giao hng!",
                      },
                    ]}
                  >
                    <Input.TextArea
                      rows={4}
                      placeholder="Nhp a ch giao hng"
                    />
                  </Form.Item>

                  <Form.Item
                    label="Phng thc thanh ton"
                    name="paymentMethod"
                  >
                    <Input.Group>
                      <Form.Item name={["paymentMethod", "method"]} noStyle>
                        <Input placeholder="Nhp phng thc thanh ton (Tin mt, Th..." />
                      </Form.Item>
                    </Input.Group>
                  </Form.Item>

                  <Form.Item>
                    <Button type="primary" htmlType="submit">
                      Xc nhn thanh ton
                    </Button>
                  </Form.Item>
                </Form>
              </Col>
            </Row>

            <Modal
              title="Xc nhn thng tin t hng"
              visible={isModalVisible}
              footer={[
                <Button key="back" onClick={handleCancel}>
                  Hy
                </Button>,
                <Button key="submit" type="primary" onClick={handleOk}>
                  ng 
                </Button>,
              ]}
            >
              <p>
                <strong>H v tn:</strong> {formValues.name}
              </p>
              <p>
                <strong>S in thoi:</strong> {formValues.phone}
              </p>
              <p>
                <strong>a ch giao hng:</strong> {formValues.address}
              </p>
              <p>
                <strong>Phng thc thanh ton:</strong>{" "}
                {formValues.paymentMethod?.method}
              </p>
              <p>
                <strong>Tng cng:</strong>{" "}
                {totalPrice.toLocaleString() + " VND"}
              </p>
            </Modal>
          </div>
        </div>
      </div>
      <Footerr />
    </>
  );
}
