import React, { useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import "./CartSlider.css"; // Gi s bn  to file CSS ny
import { useNavigate } from "react-router-dom";
import { Button } from "antd";

export default function CartSlider({isVisible}) {
  const { items } = useSelector((state) => state.cart);
  const navigate = useNavigate()
  const handleCheckout = () => {  
    console.log("click")
    navigate(`/cart`); // Chuyn n trang gi hng  
  };  
  
  return (
    <div
      className={`cart-slider ${isVisible ? "fade-in" : "fade-out"}`}
      data-aos="fade-right"
      data-aos-duration="300"
      
    >
      <h2>Gi Hng</h2>
      <Button onClick={handleCheckout}>Thanh ton</Button>
      {items.length === 0 ? (
        <p>Gi hng trng</p>
      ) : (
        items.map((item) => (
          <div className="cart-item" key={item.id}>
            <h3 className="item-name">{item.itemName}</h3>
            <p className="item-restaurant">Nh hng: {item.restaurant}</p>
            <p className="item-quantity">S lng: {item.quantity}</p>
          </div>
        ))
      )}
    </div>
  );
}
